import * as React from 'react';
import './App.css';

import { PlayerState, Screen, Source } from './shared/state'
import { connect } from 'react-redux'
import * as classnames from 'classnames'

import LoginView from './GameStates/LoginView';
import TypingView from './GameStates/TypingView';
import VotingView from './GameStates/VotingView';
import LeaderboardView from './GameStates/LeaderboardView';
import ResultsView from './GameStates/ResultsView';
import BotnikView from './BotnikView';
import ThemeRevealView from './GameStates/ThemeRevealView';

interface AppProps {
  isLoggedIn: boolean
  name?: string
  id?: string
  screen?: Screen
  showBotnikView: boolean
  botnikViewShouldExist: boolean
  source?: Source
};

class App extends React.Component<AppProps, object> {
  mainView() {
    if (this.props.isLoggedIn) {
      switch (this.props.screen) {
        case Screen.ThemeReveal:
          return <ThemeRevealView />
        case Screen.Typing:
          return <TypingView />
        case Screen.Voting:
          return <VotingView />
        case Screen.Results:
          return <ResultsView />
        case Screen.Leaderboard:
          return <LeaderboardView />
        default:
          return (
            <div>
              Wow, you're really cool, {this.props.name}. Your id is {this.props.id}
            </div>
          )
      }
    } else {
      return <LoginView />
    }
  }

  render() {
    const classNames = classnames("App", {
      "is-typing": this.props.showBotnikView
    })

    return (
      <div className={classNames}>
        {this.mainView()}
        {this.props.botnikViewShouldExist ? <BotnikView id={this.props.source!.id}/> : <span/>}
      </div>
    )
  }
};

const mapStateToProps = (state: PlayerState, ownProps: any) => {
  if (!state.gameState) {
    return {
      isLoggedIn: false,
      showBotnikView: false,
      botnikViewShouldExist: false,
      source: undefined
    }
  }

  const name = state.gameState.nameMap[state.id]

  const isLoggedIn = (name !== undefined)
  const showBotnikView = isLoggedIn &&
    state.gameState.screen === Screen.Typing &&
    state.gameState.phrases[state.id] === undefined
  const botnikViewShouldExist = isLoggedIn && state.gameState.screen !== Screen.Results

  return {
    botnikViewShouldExist,
    isLoggedIn,
    name,
    showBotnikView,
    source: state.gameState.source,
    id: state.id,
    screen: state.gameState.screen
  }
};

const mapDispatchToProps = (dispatch: any, ownProps: any) => {
  return {}
}

export default connect<AppProps>(
  mapStateToProps,
  mapDispatchToProps
)(App);



