import { Action } from './shared/actions'
import { State } from './shared/state'
import { StateBroadcast, ClientAction } from './shared/constants'

import { serverState, socketConnected } from './actionCreators'

import * as io from 'socket.io-client'

export interface GameWindow extends Window {
  gameSocket: GameSocket
}

type Dispatch = (action: Action) => void

export class GameSocket {
  dispatch: Dispatch
  socket: SocketIOClient.Socket

  constructor(dispatch: Dispatch) {
    this.dispatch = dispatch

    this.socket = io("http://localhost:8080")
    this.socket.on('connect', () => {
      this.dispatch(socketConnected(this.socket.id))
    })
    this.socket.on(StateBroadcast, (data: State) => {
      console.log(`Received message: ${JSON.stringify(data)}`)
      this.dispatch(serverState(data))
    })
  }

  sendAction(action: Action) {
    this.socket.emit(ClientAction, action)
  }
}


