import * as React from 'react';

interface Props {
  id: string
}

export default class BotnikView extends React.Component<Props> {
  iframe: HTMLIFrameElement | null

  handleMessage(message: any) {
    // TODO: Using `window` as a way to transport this data is silly
    console.log("Host received response", message);
    (window as any).message = message.data
  }

  shouldComponentUpdate() {
    return false
  }

  componentDidMount() {
    if (this.iframe) {
      this.iframe.contentWindow.postMessage("connect", "*")
    }
    window.addEventListener("message", this.handleMessage)
  }

  componentWillUnmount() {
    window.removeEventListener("message", this.handleMessage)
  }

  render() {
    const id = `botnik?source=${this.props.id}`
    return (
      <iframe
        src={id}
        ref={(iframe) => this.iframe = iframe}
      />
    )
  }
}


