export enum Screen {
  ThemeReveal = "THEME_REVEAL",
  Typing = 'TYPING',
  Voting = 'VOTING',
  Results = 'RESULTS',
  Leaderboard = 'LEADERBOARD'
}

export interface Player {
  name: string
  id: string
  score: number
}

export interface Source {
  name: string
  id: string,
  example: string,
  entry: string
}

export interface State {
  timer: number,
  screen: Screen,

  phrases: {[playerID: string]: string}
  votes: {[playerID: string]: string} // Maps to playerIDs

  players: {[playerID: string]: Player}
  spectators: {[playerID: string]: Player}
  nameMap: {[playerId: string]: string}

  source: Source
}

export interface PlayerState {
  gameState: State,
  id: string
}

export default State


