import * as React from 'react';
import { connect } from 'react-redux';

import { PlayerState } from '../shared/state'

interface Props {
  timer: number
  theme: string
  example: string
}

class ThemeRevealView extends React.Component<Props, object> {
  render() {
    const {timer, theme, example} = this.props

    return (
      <div>
        <h1>This Round's Theme</h1>
        <span>{timer}</span>
        <h2>{theme}</h2>

        <div>
          <strong>Here's an example:</strong>
          <div>{example}</div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state: PlayerState, ownProps: any) => {
  return {
    timer: state.gameState.timer,
    theme: state.gameState.source.name,
    example: state.gameState.source.example
  }
}

const mapDispatchToProps = (dispatch: any, ownProps: any) => {
  return {};
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ThemeRevealView);


