import { State } from './state'

export enum ActionID {
  JoinGame = "JoinGame",
  ServerState = "ServerState",
  SocketConnected = "SocketConnected",
  SubmitPhrase = "SubmitPhrase",
  VoteForPhrase = "VoteForPhrase",

  DecrementTimer = "DecrementTimer",
  NextScreen = "NextScreen"
}

export namespace Action {
  export interface JoinGame {
    type: ActionID.JoinGame
    value: string
  }

  export interface ServerState {
    type: ActionID.ServerState
    value: State
  }

  export interface SocketConnected {
    type: ActionID.SocketConnected
    value: string
  }

  export interface DecrementTimer {
    type: ActionID.DecrementTimer
  }

  export interface NextScreen {
    type: ActionID.NextScreen
  }

  export interface SubmitPhrase {
    type: ActionID.SubmitPhrase,
    value: string
  }

  export interface VoteForPhrase {
    type: ActionID.VoteForPhrase,
    value: string
  }
}

export type Action = Action.ServerState
  | Action.JoinGame
  | Action.SocketConnected
  | Action.DecrementTimer
  | Action.NextScreen
  | Action.SubmitPhrase
  | Action.VoteForPhrase


