import * as React from 'react';
import { connect } from 'react-redux';

import { PlayerState } from '../shared/state'
import { submitPhrase } from '../actionCreators'

interface TypingViewProps {
  timer: number
  hasSubmitted: boolean
  phrase?: string
  onSubmit: (phrase: string) => void
}

class TypingView extends React.Component<TypingViewProps, object> {
  constructor(args: any) {
    super(args)
    this.submit = this.submit.bind(this)
  }

  render() {
    if (this.props.hasSubmitted) {
      return (
        <div>
          <h1>Typing View</h1>
          <span>{this.props.timer}</span>
          <div>You've already submitted! "{this.props.phrase}"</div>
        </div>
      )
    } else {
      return (
        <div id="typing">
          <h1>Typing View</h1>
          <span>{this.props.timer}</span>
          <button onClick={this.submit}>Submit Text</button>
        </div>
      )
    }
  }

  submit() {
    // TODO: Using `window` as a way to transport this data is silly.
    const word = (window as any).message
    if (word) {
      this.props.onSubmit(word)
    }
  }
}

const mapStateToProps = (state: PlayerState, ownProps: any) => {
  return {
    timer: state.gameState.timer,
    phrase: state.gameState.phrases[state.id],
    hasSubmitted: state.gameState.phrases[state.id] !== undefined
  }
}

const mapDispatchToProps = (dispatch: any, ownProps: any) => {
  return {
    onSubmit: (phrase: string) => {
      dispatch(submitPhrase(phrase))
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TypingView);


