import * as React from 'react';

interface VotingPhraseViewProps {
  isOwnPhrase: boolean
  playerID: string
  phrase: string

  onSelect: (playerID: string) => void
}

export default class VotingPhraseView extends React.Component<VotingPhraseViewProps, object> {
  constructor(args: any) {
    super(args)
    this.onSelect = this.onSelect.bind(this)
  }

  onSelect() {
    this.props.onSelect(this.props.playerID)
  }

  render() {
    if (this.props.isOwnPhrase) {
      return <div className="phrase ownPhrase" key={this.props.phrase}>{this.props.phrase}</div>
    } else {
      return (
        <div
          className="phrase"
          key={this.props.phrase}
          onClick={this.onSelect}
        >
          {this.props.phrase}
        </div>
      )
    }
  }
}


