import * as React from 'react';
import { connect } from 'react-redux';

import * as _ from 'lodash'
import { PlayerState } from '../shared/state'

interface ResultsViewProps {
  timer: number
  results: Result[]
}

interface Result {
  name: string
  count: number
  phrase: string
  id: string
}

class ResultsView extends React.Component<ResultsViewProps, object> {
  render() {
    const results = _.chain(this.props.results)
      .sortBy((r) => r.count)
      .reverse()
      .map((r) => {
        return (
          <li key={r.id}>{r.phrase} ({r.name}): {r.count}</li>
        )
      })
      .value()

    return (
      <div>
        <h1>Results View</h1>
        <span>{this.props.timer}</span>
        <ul>
          {results}
        </ul>
      </div>
    )
  }
}

const mapStateToProps = (state: PlayerState, ownProps: any) => {
  const votes = _.countBy(state.gameState.votes)
  const phrases = {
    ...state.gameState.phrases,
    "source": state.gameState.source.entry
  }

  const results = _.chain(phrases)
    .mapValues((phrase, id): Result => {
      return {
        count: votes[id] || 0,
        name: state.gameState.nameMap[id] || "source",
        phrase,
        id
      }
    }).toArray()
    .value()

  return {
    timer: state.gameState.timer,
    results
  }
}

const mapDispatchToProps = (dispatch: any, ownProps: any) => {
  return {};
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ResultsView);


