import * as React from 'react';
import { connect } from 'react-redux';
import * as _ from 'lodash'

import { Player, PlayerState } from '../shared/state'

interface LeaderboardViewProps {
  timer: number
  players: Player[]
}

class LeaderboardView extends React.Component<LeaderboardViewProps, object> {
  render() {
    const people = _.chain(this.props.players)
    .sortBy((p) => p.score)
    .reverse()
    .map((p) => {
      return (
        <li key={p.id}>{p.name}: {p.score}</li>
      )
    })
    .value()

    return (
      <div>
        <h1>Leaderboard View</h1>
        <span>{this.props.timer}</span>
        <ul>
          {people}
        </ul>
      </div>
    )
  }
}

const mapStateToProps = (state: PlayerState, ownProps: any) => {
  return {
    timer: state.gameState.timer,
    players: _.toArray(state.gameState.players)
  }
}

const mapDispatchToProps = (dispatch: any, ownProps: any) => {
  return {};
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LeaderboardView);


