import * as React from 'react';
import { connect } from 'react-redux';
import * as _ from 'lodash'

import VotingPhraseView from '../VotingPhraseView'

import { voteForPhrase } from '../actionCreators'
import { PlayerState } from '../shared/state'

interface VotingViewProps {
  timer: number
  phrases: {string: string}
  id: string
  votedFor?: string

  onSelect: (phrase: string) => void
}

class VotingView extends React.Component<VotingViewProps, object> {
  render() {
    const phrases = _(this.props.phrases)
      .map((phrase: string, id: string) => {
        return (
          <VotingPhraseView
            isOwnPhrase={id === this.props.id}
            phrase={phrase}
            playerID={id}
            onSelect={this.props.onSelect}
            key={id}
          />
        )
      })

    console.log("RENDER", phrases, this.props.phrases)

    let votedFor
    if (this.props.votedFor) {
      votedFor = <div className='votedFor'>You voted for {this.props.votedFor}</div>
    }

    return (
      <div>
        <h1>Vote NOW!</h1>
        <h2>It's your civic duty</h2>
        {votedFor}
        <span>{this.props.timer}</span>

        <ul>
          {phrases}
        </ul>
      </div>
    )
  }
}

const mapStateToProps = (state: PlayerState, ownProps: any) => {

  const phrases = {
    ...state.gameState.phrases,
    "source": state.gameState.source.entry
  }

  let results: any = {
    phrases,
    timer: state.gameState.timer,
    id: state.id,
  }

  if (state.gameState.votes[state.id]) {
    const votedForId = state.gameState.votes[state.id]
    const votedForPhrase = phrases[votedForId]
    results.votedFor = votedForPhrase
  }

  return results
}

const mapDispatchToProps = (dispatch: any, ownProps: VotingViewProps) => {
  return {
    onSelect: (phrase: string) => {
      // TODO: This logic should happen in the component itself
      if (!ownProps.votedFor) {
        dispatch(voteForPhrase(phrase))
      }
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(VotingView);


