import * as React from 'react';
import { connect } from 'react-redux';

import { joinGame } from '../actionCreators'
import { State } from '../shared/state'

interface LoginViewProps {
  onJoinTap: (text: string) => void
}

interface LoginViewState {
  name: string
}

class LoginView extends React.Component<LoginViewProps, LoginViewState> {
  constructor(props: any) {
    super(props)
    this.state = { name: "" }

    this.onComplete = this.onComplete.bind(this)
    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(event: any) {
    this.setState({name: event.target.value})
  }

  onComplete() {
    this.props.onJoinTap(this.state.name)
  }

  render() {
    return (
      <div id='welcome'>
        <h1>What's your name?</h1>
        <input id='name' type='text' value={this.state.name} onChange={this.handleChange}/>
        <button onClick={this.onComplete}>Go!</button>
      </div>
    );
  }
}

const mapStateToProps = (state: State, ownProps: any) => {
  return {}
}

const mapDispatchToProps = (dispatch: any, ownProps: any) => {
  return {
    onJoinTap: (text: string) => {
      dispatch(joinGame(text))
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LoginView);


