import { PlayerState } from './shared/state'
import { ActionID, Action } from './shared/actions'

import { GameWindow } from './gameSocket'

export default (state: PlayerState, action: Action): PlayerState => {
  console.log("In reducer", action)
  if (action.type === ActionID.ServerState) {
    return { ...state, gameState: action.value }
  } else if (action.type === ActionID.SocketConnected) {
    return { ...state, id: action.value }
  } else {
    const socket = (window as GameWindow).gameSocket
    if (socket) {
      socket.sendAction(action)
    }
  }

  return state
}


