import { Action, ActionID } from './shared/actions'
import { State } from './shared/state'

export function joinGame(name: string): Action.JoinGame {
  return {
    type: ActionID.JoinGame,
    value: name
  }
}

export function serverState(state: State): Action.ServerState {
  return {
    type: ActionID.ServerState,
    value: state
  }
}

export function socketConnected(id: string): Action.SocketConnected {
  return {
    type: ActionID.SocketConnected,
    value: id
  }
}

export function submitPhrase(phrase: string): Action.SubmitPhrase {
  return {
    type: ActionID.SubmitPhrase,
    value: phrase
  }
}

export function voteForPhrase(playerID: string): Action.VoteForPhrase {
  return {
    type: ActionID.VoteForPhrase,
    value: playerID
  }
}


